import { useState, useEffect, useRef } from "react";
import { SWRConfig } from "swr";
import { Web3ReactProvider, useWeb3React } from "@web3-react/core";
import { Web3Provider } from "@ethersproject/providers";

import {
  useChainId,
  getInjectedHandler,
  useEagerConnect,
  useInactiveListener,
  activateInjectedProvider,
  SHOULD_EAGER_CONNECT_LOCALSTORAGE_KEY,
  CURRENT_PROVIDER_LOCALSTORAGE_KEY,
} from "../lib/legacyOptions";
import { Exchange } from "../pages/Exchange/ExchangeOptions";

import { cssTransition, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import "../styles/Font.css";
import "./App.scss";
import "../styles/Input.scss";
import useEventToast from "../components/EventToast/useEventToast";
import EventToastContainer from "../components/EventToast/EventToastContainer";
import { defaultLocale, dynamicActivate } from "../lib/i18n";
import _AccountManager from "../../../deployments/arbitrum_ddl/accountManager.json";

if ("ethereum" in window) {
  window.ethereum.autoRefreshOnNetworkChange = false;
}

function getLibrary(provider) {
  const library = new Web3Provider(provider);
  return library;
}

const Jelly = cssTransition({
  enter: "jellyIn",
  exit: "jellyOut",
});


function FullApp(props) {
  const exchangeRef = useRef();
  const { connector, activate } = useWeb3React();
  const { chainId } = useChainId();
  useEventToast();
  const [activatingConnector, setActivatingConnector] = useState();
  useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined);
    }
  }, [activatingConnector, connector, chainId]);
  const triedEager = useEagerConnect(setActivatingConnector);
  useInactiveListener(!triedEager || !!activatingConnector);

  useEffect(() => {
    attemptActivateWallet("MetaMask");
  }, [props.walletAddress])
  const connectWallet = () => {
    props.connectWallet();
    attemptActivateWallet("MetaMask");
  };
  const connectInjectedWallet = getInjectedHandler(activate);

  const attemptActivateWallet = (providerName) => {
    localStorage.setItem(SHOULD_EAGER_CONNECT_LOCALSTORAGE_KEY, true);
    localStorage.setItem(CURRENT_PROVIDER_LOCALSTORAGE_KEY, providerName);
    activateInjectedProvider(providerName);
    connectInjectedWallet();
  };


  return (
    <>
      <div className="App GMX-Interface Options-Interface">
        <div className="App-content">
          <Exchange
            ref={exchangeRef}
            connectWallet={connectWallet}
            walletAddress={props.walletAddress}
          />
        </div>
      </div>
      <ToastContainer
        limit={1}
        transition={Jelly}
        position="bottom-right"
        // autoClose={}
        hideProgressBar={true}
        newestOnTop={false}
        closeOnClick={false}
        draggable={false}
        pauseOnHover
      />
      <EventToastContainer />
    </>
  );
}

function Options(props) {
  useEffect(() => {
    const defaultLanguage = defaultLocale;
    dynamicActivate(defaultLanguage);
  }, []);

  return (
    <SWRConfig value={{ refreshInterval: 5000 }}>
      <Web3ReactProvider getLibrary={getLibrary}>
        <FullApp {...props} />
      </Web3ReactProvider>
    </SWRConfig>
  );
}

export default Options;
